.elite {
	padding: 158px 0 82px;
	max-width: 1900px;
	margin: 0 auto;
	@include r(1400) {
		zoom: .7; }
	&__heading {
		color: #1f1f1f;
		font-family: Akrobat;
		font-size: 50px;
		text-transform: uppercase;
		text-align: center;
		margin-bottom: 52px; }
	&__list {
		padding: 0 20px;
		max-width: 1900px;
		margin: 0 auto;
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: center; }
	&__image {
		position: relative;
		margin-bottom: 60px;
		max-height: 630px;
		img {
			max-height: 560px; } }
	&__item {
		position: relative;
		margin: 0 25px; }
	&__caption {
		padding: 20px 20px 13px 60px;
		position: absolute;
		background-color: rgba(255,255,255, .2);
		top: 40px;
		left: 0;
		right: 0;
		color: #fff; }
	&__title {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 26px;
		text-transform: uppercase;
		max-width: 252px;
		border-bottom: 2px solid #6ac0f3;
		padding-bottom: 17px;
		margin-bottom: 15px; }
	&__text {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 18px;
		letter-spacing: 1.14px; }
	&__price {
		text-align: center;
		color: #000000;
		font-family: Akrobat;
		font-size: 30px;
		text-transform: uppercase;
		// Text style for "о, т  ,  р"
		font-weight: 300;
 }		// Text style for "652 000"
	&__size-price {
		color: #d24a43;
		font-family: Akrobatbold;
		font-size: 36px;
		font-weight: 700; } }


