.footer {
	background-color: #525252;
	padding: 85px 0 67px;
	&__wrap {
		max-width: 1200px;
		margin: 0 auto;
		display: flex;
		-webkit-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		justify-content: space-between;
		color: #dcdcdc;
		font-family: Akrobat;
		font-size: 16px;
 }		// Text style for "деревья  ,"
	&__title {
		text-transform: uppercase;
		&:after {
			display: block;
			content: '';
			width: 106px;
			height: 2px;
			background-color: #6ac0f3;
			margin-top: 24px;
			margin-bottom: 27px; } }
	&__list {
		li {
			margin-bottom: 16px; }
		a {
			color: #dcdcdc;
			font-family: Akrobat;
			font-size: 16px;
			text-decoration: none; } }
	&__contacts {
		text-align: center;
		border: 2px solid #6ac0f3;
		padding: 25px 25px 38px;
		font-size: 16px; }
	&__phone {
		color: #dcdcdc;
		font-family: Akrobatbold;
		margin-bottom: 35px; }
	&__hour-work {
		margin-bottom: 35px; }
	&__email {
		margin-bottom: 30px; } }

