.features {
	padding: 115px 0 115px;
	&__list {
		width: 881px;
		margin: 0 auto; }
	&__item {
		padding: 0 40px;
		display: inline-block;
		width: 24%;
		color: #000000;
		font-family: Akrobat;
		font-size: 26px;
		font-weight: 300;
		text-transform: uppercase;
		text-align: center;
		&:before {
			display: block;
			content: '';
			@include s(gift);
			transform: scale(.5); }
		&_logistick:before {
			margin-left: -14px;
			@include s(logistick); }
		&_design:before {
			@include s(design); }
		&_nature:before {
			@include s(nature); }
		&_car-small:before {
			@include s(car-small); }
		&_car-manipulator:before {
			@include s(car-manipulator); }
		&_car-big:before {
			@include s(car-big);
			margin-left: -20px; }
		&_car-croups:before {
			@include s(car-croups); } } }
