  border: 1px solid rgba(98, 98, 98, 0.5);
  border-bottom: none;
  border-right: none;
}

.info-table:before {
  position: absolute;
  margin-top: -1px;
  top: 50%;
  display: block;
  content: '';
  width: 600px;
  height: 1px;
  background: url("../img/bg-table-2.png") 0 50% no-repeat;
