  content: '';
  background-color: red;
  width: 60px;
  height: 80px;
  position: absolute;
  bottom: 0;
  background: #626262 url("../img/ruble.png") 50% 50% no-repeat;
  border: 1px solid #e8e8e8;
  border-left: none;
}

.info-table__row_first :before {
  position: absolute;
  display: block;
  position: absolute;
  display: block;
  content: '';
  height: 120px;
  width: 1px;
  background: url("../img/bg-table.png") 50% 0 no-repeat;
  background-size: cover;
  top: 0;
  right: 0;
}

.info-table__row_grow {
  background-color: #626262;
  border: 1px solid rgba(232, 232, 232, 0.5);
  border-top: none;
}

