  color: #fff;
  position: relative;
}

.info-table__row_grow td:before {
  position: absolute;
  display: block;
  content: '';
  height: 200%;
  width: 1px;
  background: url("../img/bg-table.png") 50% 0 no-repeat;
  background-size: cover;
  top: -100%;
}

.info-table__row_grow .hov:hover, .info-table__row_grow .hov-active {
  background-color: #ffd357;
  cursor: url("../img/cursor.png"), pointer;
  color: #000;
}

.info-table__row_grow .hov:hover:after, .info-table__row_grow .hov-active:after {
  display: block;
  content: '';
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: -20px;
  background-color: #ffd357;
}

